import { useEffect, useRef, useState } from 'react';
import { animated, useTransition } from 'react-spring';
import { PillButton } from '~/common/ui-elements';

const COUNTDOWN = 3;

export const HaloCountdown = () => {
  const [currentTime, setCurrentTime] = useState(COUNTDOWN);
  const [showCountdown, setShowCountdown] = useState(false);
  const intervalRef = useRef<NodeJS.Timer | null>(null);

  const transitions = useTransition(showCountdown, {
    from: { translateY: 'translateY(-50%)' },
    enter: { translateY: 'translateY(0)' },
    leave: { translateY: 'translateY(-50%)' },
    delay: 200,
  });

  useEffect(() => {
    if (!showCountdown) {
      return;
    }

    intervalRef.current = setInterval(() => {
      setCurrentTime((prevTime) => prevTime - 1);
    }, 1000);
  }, [showCountdown]);

  useEffect(() => {
    if (currentTime === 0 && intervalRef.current) {
      clearInterval(intervalRef.current);

      setShowCountdown(false);
      setCurrentTime(COUNTDOWN);
    }
  }, [currentTime]);

  return (
    <div className="flex flex-col items-center">
      <div className="mb-10 w-full h-full dark:text-theme-white text-theme-black text-lg relative">
        {showCountdown && (
          <>
            {transitions(({ opacity }, item) => {
              return <animated.span>{currentTime}</animated.span>;
            })}
          </>
        )}
      </div>

      <PillButton
        className="bg-theme-blue"
        disabled={showCountdown === true}
        onClick={() => {
          if (showCountdown) {
            return;
          }

          setShowCountdown(true);
        }}
      >
        Start
      </PillButton>
    </div>
  );
};
